.pokemon-list {
  display: flex;
  row-gap: 1.5rem;
  justify-content: center;
  margin: 0.5rem 0;
  flex-wrap: wrap;
  padding: 1rem 7rem;
}

.pokemon-card {
  width: 11rem;
  border: 1px solid var(--border-color);
  border-radius: 3%;
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: 0 1rem;
}

.pokemon-image-container {
  width: 100%; 
}

.pokemon-image {
  width: 100%;
}

.pokemon-name {
  width: 100%;
  border-top: 1px solid var(--border-color);
  text-align: center;
  background-color: var(--name-background);
  font-size: 0.8rem;
  font-weight: 600;
  padding: 0.2rem;
}

.hide-card {
  display: none;
}

@media (min-width:0px) and (max-width:576px) {
  .pokemon-list {
    padding: 1rem 4rem;
  }
}

@media (min-width:576px) and (max-width:768px) {
  .pokemon-list {
    padding: 1rem 5rem;
  }
}