@import url(https://fonts.googleapis.com/css2?family=Inter:wght@100;200;300;400;500;600;700;800;900&display=swap);
:root {
  --navbar-color: #F8F9F9;
  --brand-color: #000000;
  --search-font-color:#8685858a;
  --border-color: #cecfcfd0;
  --backdrop-bg: #00000080;
  --modal-bg: #ffffff;
  --button-compare-bg: #6c757d;
  --mobile-font-size: 0.7rem;
  --desktop-font-size: 0.9rem;
  --title-comparison-color: #757272;
  --box-shadow-border: #e7e7e7;
  --comparison-mobile-font-size: 0.5rem;
  --comparison-desktop-font-size: 0.8rem;
  --button-font-color: #fec204;
  --name-background: #f5f5f5;
  --input-shadow: #86c5f0; 
}

* {
  font-family: 'Inter', sans-serif;
  box-sizing: border-box;
}

body {
  margin: 0;
}


.nav-bar {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  background-color: var(--navbar-color);
}

.main-nav {
  display: flex;
  align-items: center;
}

.brand-name {
  font-size: 1.3rem;
  color: var(--brand-color);
  margin: 1rem;
  font-weight: 400;
}

.nav-item {
  font-size: 1rem;
  font-weight: 700;
  color: var(--brand-color);
}

.search-box {
  width: 12rem;
  height: 2.5rem;
  margin: 0.5rem 1rem;
  padding: 1rem; 
  border: 1px solid var(--border-color);
  border-radius: 0.3rem;
  outline: none;
}

.search-box:focus {
  box-shadow: 0 0 2px 3px var(--input-shadow); 
}

.search-box::-webkit-input-placeholder {
  font-size: 1rem;
  font-weight: 400;
}

.search-box:-ms-input-placeholder {
  font-size: 1rem;
  font-weight: 400;
}

.search-box::-ms-input-placeholder {
  font-size: 1rem;
  font-weight: 400;
}

.search-box::placeholder {
  font-size: 1rem;
  font-weight: 400;
}

@media (min-width:0px) and (max-width:576px) {
  .nav-item {
    font-size: 0.7rem;
  }

  .brand-name {
    font-size: 1rem;
  }

  .search-box {
    width: 7rem;
    margin: 0.5rem ;
  }

  .search-box::-webkit-input-placeholder {
    font-size: 0.8rem;
    font-weight: 400;
  }

  .search-box:-ms-input-placeholder {
    font-size: 0.8rem;
    font-weight: 400;
  }

  .search-box::-ms-input-placeholder {
    font-size: 0.8rem;
    font-weight: 400;
  }

  .search-box::placeholder {
    font-size: 0.8rem;
    font-weight: 400;
  }
}
.pokemon-list {
  display: flex;
  grid-row-gap: 1.5rem;
  row-gap: 1.5rem;
  justify-content: center;
  margin: 0.5rem 0;
  flex-wrap: wrap;
  padding: 1rem 7rem;
}

.pokemon-card {
  width: 11rem;
  border: 1px solid var(--border-color);
  border-radius: 3%;
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: 0 1rem;
}

.pokemon-image-container {
  width: 100%; 
}

.pokemon-image {
  width: 100%;
}

.pokemon-name {
  width: 100%;
  border-top: 1px solid var(--border-color);
  text-align: center;
  background-color: var(--name-background);
  font-size: 0.8rem;
  font-weight: 600;
  padding: 0.2rem;
}

.hide-card {
  display: none;
}

@media (min-width:0px) and (max-width:576px) {
  .pokemon-list {
    padding: 1rem 4rem;
  }
}

@media (min-width:576px) and (max-width:768px) {
  .pokemon-list {
    padding: 1rem 5rem;
  }
}
.container {
  display: flex;
  justify-content: center;
}

.backdrop {
  visibility: hidden;
  opacity: 0;
  position: fixed;
  z-index: 1;
  background: var(--backdrop-bg);
  width: 100vw;
  height: 100vh;
  justify-content: center;
  align-items: center;
  transition: opacity 200ms linear;
}

.modal {
  visibility: hidden;
  opacity: 0;
  transform: translateY(-3rem);
  background-color: var(--modal-bg);
  width: auto;
  position: fixed;
  z-index: 2;
  border-radius: 0.3rem;
  top: 12%;
  transition: opacity 300ms ease-out, transform 500ms ease-out;
}

.open {
  visibility: visible;
  transform: translateY(0);
  opacity: 1;
}

.modal-header {
  display: flex;
  justify-content: space-between;
  border-bottom: 1px solid var(--border-color);
  align-items: center;
  padding: 1rem;
}

.modal-header-items {
  display: flex;
  align-items: center;
}

.modal-pokemon-name {
  font-weight: 600;
  font-size: 1.1rem;
}

.compare-button {
  cursor: pointer;
  margin: 0 0.5rem;
  height: 1.8rem;
  font-size: 0.8rem;
  color: var(--modal-bg);
  background: var(--button-compare-bg);
  outline: none;
  border-radius: 0.2rem;
  border: 1px solid var(--button-compare-bg);
}

.out-modal {
  color: var(--button-compare-bg);
  font-weight: 500;
  font-size: 1rem;
  cursor: pointer;
}

.modal-body {
  display: flex;
  border-bottom: 1px solid var(--border-color) ;
  margin: 1rem;
}

.modal-image-container {
  width: 10rem;
  margin-right: 1rem;
  margin-top: 1rem;
}

.modal-image {
  width: 100%;
}

.modal-pokemon-details {
  width: 18rem;
}

.details-description {
  font-size: var(--desktop-font-size);
  border-bottom: 1px solid var(--border-color);
}

.details-features {
  display: flex;
  flex-wrap: wrap;
  grid-column-gap: 2rem;
  -webkit-column-gap: 2rem;
          column-gap: 2rem;
  margin: 1rem 0;
}

.feature {
  display: flex;
  flex-direction: column;
}

.feature-name {
  font-size: var(--desktop-font-size);
  font-weight: 600;
  margin: 0;
}

.feature-value {
  margin: 0.5rem 0;
  font-size: var(--desktop-font-size);
}

.modal-chart {
  position: relative;
  width: auto;
  height: auto;
  padding: 0 1rem;
  display: flex;
  justify-content: center;
  align-items: center;
}

.modal-comparison-images {
  display: flex;
  margin: 0 2rem;
  grid-column-gap: 5rem;
  -webkit-column-gap: 5rem;
          column-gap: 5rem;
  border-bottom: 1px solid var(--border-color);
}

.comparison-image-container {
  width: 10rem;
}

.comparison-image {
  width: 100%;
}

.comparison-features {
  display: flex;
  justify-content: center;
  grid-column-gap: 5rem;
  -webkit-column-gap: 5rem;
          column-gap: 5rem;
  text-align: center;
  padding: 1rem 0;
  margin: 0 2rem;
  border-bottom: 1px solid var(--border-color);
}

.comparison-feature-name {
  font-size: var(--desktop-font-size);
  font-weight: 600;
  padding: 0.2rem 0;
}

.comparison-feature-value {
  font-size: var(--desktop-font-size);
  padding: 0.2rem 0;
}

.modal-chart-mixed {
  position: relative;
  width: auto;
  height: auto;
  padding: 0 1rem;
  display: flex;
  justify-content: center;
  align-items: center;
}

@media (min-width:0px) and (max-width:576px) {
  .modal {
    top: 15%;
  }

  .modal-body {
    margin: 0.5rem; 
  }

  .modal-image-container {
    width: 7rem;
    max-width: 8rem;
    margin-right: 0.5rem;
    margin-top: 0.5rem;
  }

  .modal-pokemon-details {
    width: 12rem;
    max-width: 12rem;
  }

  .modal-chart {
    width: auto;
  }

  .details-description {
    font-size: var(--mobile-font-size);
  }
  
  .details-features {
    grid-column-gap: 1rem;
    -webkit-column-gap: 1rem;
            column-gap: 1rem;
  }
  
  .feature-name {
    font-size: var(--mobile-font-size);
  }
  
  .feature-value {
    font-size: var(--mobile-font-size);
  }
  
  .modal-comparison-images {
    margin: 0 1rem;
    grid-column-gap: 2.5rem;
    -webkit-column-gap: 2.5rem;
            column-gap: 2.5rem;
    justify-content: center;
  }
  
  .comparison-image-container {
    width: 8rem;
    margin: 0;
  }
  
  .comparison-features {
    grid-column-gap: 3rem;
    -webkit-column-gap: 3rem;
            column-gap: 3rem;
    margin: 0 2rem;
  }
  
  .comparison-feature-name {
    font-size: var(--mobile-font-size);
  }
  
  .comparison-feature-value { 
    font-size: var(--mobile-font-size);
  }
  
  .modal-chart-mixed {
    width: auto;  
  }
}
.comparison-card {
  visibility: hidden;
  opacity: 0;
  background: var(--modal-bg);
  position: fixed;
  right: 1.5rem;
  top: 10%;
  border: 1px solid var(--border-color);
  border-radius: 0.2rem;
  box-shadow: 0 2px 5px 1px var(--box-shadow-border);
  transition: opacity 1000ms ease-out;
}

.open-comparison {
   visibility: visible;
   opacity: 1;
}

.comparison-header {
  font-size: var(--comparison-desktop-font-size);
  font-weight: 600;
  border-bottom: 1px solid var(--border-color);
  color: var(--title-comparison-color);
  margin: 0;
  padding: 0.3rem 0.5rem;
  text-align: center;
  margin-bottom: 0.2rem;
}

.comparison-item {
  padding: 0.6rem 0.5rem;
  font-size: var(--comparison-desktop-font-size);
  font-weight: 600;
}

@media (min-width:0px) and (max-width:576px) {
  .comparison-card {
    right: var(--comparison-mobile-font-size);
  }

  .comparison-header {
    font-size: var(--comparison-mobile-font-size);
    width: 5rem;
  }

  .comparison-item {
    font-size: var(--comparison-mobile-font-size);
  }
}

@media (min-width:576px) and (max-width:768px) {
  .comparison-card {
    right: 1rem;
  }
  
  .comparison-header {
    font-size: var(--mobile-font-size);
    width: 7rem;
  }

  .comparison-item {
    font-size: var(--mobile-font-size);
  }
}
.home {
  background: url(../../static/media/image1.0273dd00.jpg) center/cover no-repeat;
  width: 100vw;
  height: 100vh;
  -webkit-animation: changeBackground 15s infinite ease-out;
          animation: changeBackground 15s infinite ease-out;
}

.container-pokemon-title {
  width: 30rem;
}

.pokemon-title-image {
  width: 100%;
}

.pokemon-title-image:hover {
  -webkit-animation: wiggle 400ms 3s 8 ease-out;
          animation: wiggle 400ms 3s 8 ease-out;
}

.backdrop-home {
  background: var(--backdrop-bg);
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

@-webkit-keyframes wiggle {
  0% {
    transform: rotateZ(0);
  }
  50% {
    transform: rotateZ(-10deg);
  }
  100% {
    transform: rotateZ(10deg);
  }
}

@keyframes wiggle {
  0% {
    transform: rotateZ(0);
  }
  50% {
    transform: rotateZ(-10deg);
  }
  100% {
    transform: rotateZ(10deg);
  }
}

@-webkit-keyframes changeBackground {
  0%, 25% {
    background: url(../../static/media/image1.0273dd00.jpg) center/cover no-repeat;
  }
  33%, 58% {
    background: url(../../static/media/image2.a434fa69.jpg) center/cover no-repeat;
  }
  66%, 91% { 
    background: url(../../static/media/image3.4613840b.png) center/cover no-repeat;
  }
}

@keyframes changeBackground {
  0%, 25% {
    background: url(../../static/media/image1.0273dd00.jpg) center/cover no-repeat;
  }
  33%, 58% {
    background: url(../../static/media/image2.a434fa69.jpg) center/cover no-repeat;
  }
  66%, 91% { 
    background: url(../../static/media/image3.4613840b.png) center/cover no-repeat;
  }
}

.welcome {
  color: var(--modal-bg);
  font-size: 2rem;
  font-weight: 800;
  text-align: center;
  padding: 1rem;
}

.welcome-description {
  color: var(--modal-bg);
  font-size: 1rem;
  font-weight: 500;
  padding: 2rem;
  text-align: center;
}

.goto-pokemons { 
  width: 15rem;
  height: 3rem;
  background-color: transparent;
  border: 1px solid var(--modal-bg);
  border-radius: 2rem;
  font-size: 1rem;
  color: var(--modal-bg);
  cursor: pointer;
  outline: none;
  font-weight: 800;
  text-decoration: none;
}

.goto-pokemons:hover {
  background: var(--modal-bg);
  color: var(--button-font-color) ;
  border: 1px solid var(--button-font-color);
}

@media (min-width:0px) and (max-width:576px) {
  .container-pokemon-title {
    width: 20rem;
  }
}
